exports.push([module.id, "\n.group[data-v-5154d3b5]{\n    background: #f3f3f3;\n    font-size: 16px;\n    line-height: 50px;\n    text-indent: 10px;\n}\n.menu[data-v-5154d3b5] {\n    border-right: 1px solid #eee;\n.router-link {\n        display: inline-block;\n        width: 100%;\n        padding: 14px 24px;\n        color: #495060;\n.title{\n            padding: 0px 5px;\n}\n}\nul {\n        position: relative;\n}\n.router-link-active {\n        color: #2d8cf0;\n        border-right: 2px solid #2d8cf0;\n        /*animation: menu-transform 0.5s;*/\n        /*-moz-animation: menu-transform 0.5s;\t!* Firefox *!*/\n        /*-webkit-animation: menu-transform 0.5s;\t!* Safari 和 Chrome *!*/\n        /*-o-animation: menu-transform 0.5s;\t!* Opera *!*/\n}\n}\n@keyframes menu-transform-data-v-5154d3b5 {\nfrom {\n        transform: rotate(-90deg);\n        -ms-transform: rotate(-90deg); /* IE 9 */\n        -moz-transform: rotate(-90deg); /* Firefox */\n        -webkit-transform: rotate(-90deg); /* Safari 和 Chrome */\n        -o-transform: rotate(-90deg); /* Opera */\n}\nto {\n        transform: rotate(0deg);\n        -ms-transform: rotate(0deg); /* IE 9 */\n        -moz-transform: rotate(0deg); /* Firefox */\n        -webkit-transform: rotate(0deg); /* Safari 和 Chrome */\n        -o-transform: rotate(0deg); /* Opera */\n}\n}\n\n", "", {"version":3,"sources":["/Users/fanxinqi/Desktop/洗衣帮项目/xyb-fe/sunstrider/src/fragment/src/fragment/menu.vue"],"names":[],"mappings":";AAaA;IACA,oBAAA;IACA,gBAAA;IACA,kBAAA;IACA,kBAAA;CACA;AACA;IACA,6BAAA;AACA;QACA,sBAAA;QACA,YAAA;QACA,mBAAA;QACA,eAAA;AACA;YACA,iBAAA;CACA;CACA;AAEA;QACA,mBAAA;CACA;AAEA;QACA,eAAA;QACA,gCAAA;QACA,mCAAA;QACA,sDAAA;QACA,iEAAA;QACA,kDAAA;CACA;CAEA;AAEA;AACA;QACA,0BAAA;QACA,8BAAA,CAAA,UAAA;QACA,+BAAA,CAAA,aAAA;QACA,kCAAA,CAAA,qBAAA;QACA,6BAAA,CAAA,WAAA;CACA;AACA;QACA,wBAAA;QACA,4BAAA,CAAA,UAAA;QACA,6BAAA,CAAA,aAAA;QACA,gCAAA,CAAA,qBAAA;QACA,2BAAA,CAAA,WAAA;CACA;CACA","file":"menu.vue","sourcesContent":["<template>\n    <div class=\"menu\">\n        <Menu :theme=\"theme3\" @on-select=\"select\" mode=\"horizontal\" active-key=\"1\" v-for=\"(group,gidx) in menuGroups\" :key=\"gidx\">\n            <!-- <Menu-group v-for=\"(group,gidx) in menuGroups\" :key=\"gidx\" :title=\"group.groupName\" > -->\n                <Menu-item :key=\"menu.name\" :name=\"menu.name\" v-for=\"(menu,idx) in group.menus\"  >\n                    <Icon :type=\"menu.icon\"></Icon><span class=\"title\">{{menu.title}}</span>\n                    <!-- <router-link :to=\"menu.path\" :class=\"['router-link', {'router-link-active':menu.isCurrent}]\"></router-link> -->\n                </Menu-item>\n            <!-- </Menu-group> -->\n        </Menu>\n    </div>\n</template>\n<style scoped>\n    .group{\n        background: #f3f3f3;\n        font-size: 16px;\n        line-height: 50px;\n        text-indent: 10px;\n    }\n    .menu {\n        border-right: 1px solid #eee;\n        .router-link {\n            display: inline-block;\n            width: 100%;\n            padding: 14px 24px;\n            color: #495060;\n            .title{\n                padding: 0px 5px;\n            }\n        }\n\n        ul {\n            position: relative;\n        }\n\n        .router-link-active {\n            color: #2d8cf0;\n            border-right: 2px solid #2d8cf0;\n            /*animation: menu-transform 0.5s;*/\n            /*-moz-animation: menu-transform 0.5s;\t!* Firefox *!*/\n            /*-webkit-animation: menu-transform 0.5s;\t!* Safari 和 Chrome *!*/\n            /*-o-animation: menu-transform 0.5s;\t!* Opera *!*/\n        }\n\n    }\n\n    @keyframes menu-transform {\n        from {\n            transform: rotate(-90deg);\n            -ms-transform: rotate(-90deg); /* IE 9 */\n            -moz-transform: rotate(-90deg); /* Firefox */\n            -webkit-transform: rotate(-90deg); /* Safari 和 Chrome */\n            -o-transform: rotate(-90deg); /* Opera */\n        }\n        to {\n            transform: rotate(0deg);\n            -ms-transform: rotate(0deg); /* IE 9 */\n            -moz-transform: rotate(0deg); /* Firefox */\n            -webkit-transform: rotate(0deg); /* Safari 和 Chrome */\n            -o-transform: rotate(0deg); /* Opera */\n        }\n    }\n\n</style>\n\n<script>\n    import router from '../router/router';\n    import config from '../config';\n    import dao from '../dao/dao';\n    console.log(config)\n    const routes = router.getRules();\n    const cache = [];\n    //获取所有原始所有路由\n    const genRoutesMenu = function () {\n        return routes.map((node) => {\n            return {name: node.name, title: node.title.pick(this.$store.state).get('title'), path:node.path};\n        });\n    };\n    //获取菜单\n    const getMenus=function(){\n        if(cache.length>0) return cache;\n        let me=this;\n        let routers=genRoutesMenu.call(this);\n        let hideNum=0\n        let isHide=false;\n        config.MENU_GROUP.forEach(function(item){\n                let menus=[];\n              \n                for(let i in item.menus){\n                    const routeName= item.menus[i].name\n                    let router= routers.find(function(value, index, arr) {\n                        return value.name ===routeName;\n                    })||{}\n                    if(router.name===me.$route.name){\n                        router[\"isCurrent\"]=true;\n                    }\n                    else{\n                        router[\"isCurrent\"]=false;\n                    }\n                    router[\"isShow\"]=true;\n                    router[\"icon\"]=item.menus[i].icon || \"\"\n                    hideNum++\n                    menus.push(router);\n                }\n                if(hideNum===menus.length){\n                    isHide=true;\n                }\n                else{\n                    isHide=false;\n                }\n                cache.push({\n                    groupName:item.name,\n                    isHide,\n                    menus\n                })\n        })\n        return cache;\n    }\n    const Menu = {\n        data() {\n            return {\n                menuGroups:getMenus.call(this),\n                currentRouterName: this.$route.name,\n                activeName: \"\",\n                theme3:\"dark\"\n            };\n        },\n\n        created() {\n//            this.menu-groups=;\n//            console.log(this.menu-groups)\n          //  this.menus = genRoutesMenu(this.$store.state, this.$route.params);\n//            this.\n        },\n        watch:{\n           $route(){\n               let me=this;\n               console.log(\"router change\")\n               this.menuGroups.forEach(function(group){\n                   group.menus.forEach(function(menu){\n                      if(menu.name===me.$route.name){\n                          menu[\"isCurrent\"]=true;\n                      }\n                      else{\n                          menu[\"isCurrent\"]=false;\n                      }\n\n                   })\n               });\n           }\n        },\n        computed: {\n            roleIds:function(){\n                return this.$store.state[\"current-user\"][\"roleIds\"]\n            }\n        },\n        methods:{\n            select(key){\n                console.log(key)\n                this.$router.push(key);\n            }\n        }\n    };\n    export default Menu\n\n</script>\n\n"],"sourceRoot":""}]);

